* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

.bgc {
  height: 630px;
  width: 100%;
  background: linear-gradient(to bottom right, #007fc2, #00d6ee);
}

.section1 {
  padding: 150px 250px 0px 250px;
}

h1 {
  font-family: sans-serif;
  font-weight: 700;
  color: #fff;
  font-size: 20px;
  padding-bottom: 30px;
}

.clearfix:before,
.clearfix:after {
  content: '';
  display: table;
  clear: both;
}

.card1 {
  padding-top: 190px;
  height: 500px;
  width: 350px;
  float: left;
  background: #fff;
  border-radius: 10%;
}

.circle {
  padding-top: 140px;
  padding-left: 10px;
  width: 70px;
  height: 70px;
  background: rgba(0, 0, 0, 0);
  border-radius: 50%;
}

.photo1 {
  width: 100px;
  height: 100px;
  display: inline-block;
  margin-left: -100px;
  border-radius: 50%;
}